$prefix: '.rise-blocks';

#{$prefix}-accordion-item {
    .ui-accordion-header {
        display: flex !important;
        align-items: center;
        justify-content: space-between;
        border: none;

        >span {
            order: 2;
        }

        >h3,
        >p {
            margin: 0;
            order: 1;
            flex: 0 0 60%;

            a {
                display: block;

                &:focus {
                    outline: none;
                }
            }
        }

        &:focus {
            outline: none;
        }
    }

    .ui-widget-content {
        border: none;
    }
}

.wp-block-rise-blocks-accordion-item {
    margin-bottom: 20px !important;

    &:last-child {
        margin-bottom: 0px !important;
    }

    .ui-icon {
        text-indent: inherit !important;
        background-image: none;
        overflow: visible;
        margin-top: 0;
        height: auto;
        width: auto;

        &:after {
            content: "\f061";
            font-family: fontAwesome;
            height: calc(35px + .86em);
            width: 60px;
            position: absolute;
            background-size: 35px;
            right: -8px;
            background-repeat: no-repeat;
            top: 50%;
            transform: translate(0, -50%) rotate(0);
            transition: all .3s;
            background: transparent;
            color: #000;
            align-items: center;
            justify-content: center;
            display: flex;
        }
    }

    .ui-state-active {
        .ui-icon {
            &:after {
                content: "\f063";
                color: #fff;
            }
        }
    }
}

#{$prefix}-accordion-item {
    position: relative;

    #{$prefix}-banner-section-wrapper {
        position: relative;
    }

    #{$prefix}-page-slider-overlay {
        position: absolute;
        height: 100%;
        width: 100%;
        background: #000000;
        z-index: 999;
        left: 0;
        transition: all .3s;
        min-height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;

        &:after {
            content: '';
            border: 5px solid #f3f3f3;
            border-radius: 50%;
            border-top: 5px solid #009990;
            width: 60px;
            height: 60px;
            -webkit-animation: spin 2s linear infinite;
            /* Safari */
            animation: spin 2s linear infinite;
        }
    }
}

/* Safari */
@-webkit-keyframes spin {
    0% {
        -webkit-transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(360deg);
    }
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}